.cartitems{
    margin: 100px 170px;
}

.cartitems hr{
    height: 3px;
    background: #e2e2e2;
    border: 0;
}

.cartitems-format-main{
    display: grid;
    grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
    align-items: center;
    gap: 75px;
    padding: 20px 0px;
    color: #454545;
    font-size: 18px;
    font-weight: 600;
}

.cartitems-format{
    font-size: 17px;
    font-weight: 700;
}

.carticon-product-icon{
height: 62px;

}

.cartitems-remove-item{
    width: 15px;
    margin: 0px 40px;
    cursor: pointer;
}

.cartitems-quantity{
    width: 64px;
    height: 50px;
    border: 2px solid #ebebeb;
    background: #fff;
}

.cartitems-down{
    display: flex;
    margin: 100px 0px;
}

.cartitems-total{
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-right: 200px;
    gap: 40px;
}

.cartitem-total-item{
    display: flex;
    justify-content: center;
    padding: 15px 0px;
}

.cartitems-total button{
    width: 262px;
    height: 58px;
    outline: none;
    border: none;
    background: #ff5a5a;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
}

.cartitems-promocode{
    flex: 1;
    font-size: 16px;
    font-weight: 500;
}

.cartitems-promocode p{
    color:#555 ;
}

.cartitems-promobox{
    width: 504px;
    margin-top: 15px;
    /* padding-left: 20px; */
    height: 58px;
    border: 1px solid #eaeaea;
}

.cartitems-promobox input{
    border: none;
    outline: none;
    border: transparent;
    font-size: 16px;
    width: 330px;
    height: 50px;
}

.cartitems-promobox button{
    width: 170px;
    height: 58px;
    font-size: 16px;
    background: #000;
    color: #fff;
    cursor: pointer;
}

@media only screen and (min-width:320px) and (max-width:720px){
    .cartitems{
        margin: 0;
        padding: 15px;
    }
    .cartitems-format-main{
        gap: 12px;
        font-size: 10px;
        /* grid-template-columns: 1fr 1fr; */
    }
    .cartitems-format{
        gap: 12px;
        font-size: 10px;
    }

    .cartitems-down{
        flex-direction: column;
        margin:0px;
    }

    .cartitems-total{
        margin-right: 0;
        align-items: center;
        gap:0px;
    }
    .cartitems-promocode{
        padding: 15px;
        text-align: center;
    }
    .cartitems-promobox{
        width: 100%;
        padding-left: 0;
        margin-bottom: 50px;
    }
    .cartitems-promobox button{
        width: 100%;
    }
}